"use client";
import React, { useState, useEffect, useMemo, useCallback } from "react";
import axios from "axios";
import Sidebar from "../components/Sidebar";
import Link from "next/link";
import { useRouter } from "next/navigation";

type User = {
  id: string;
  name: string;
  phone: string;
  address: string;
  role: "user" | "admin" | "manager";
  email?: string;
};

type UserCardProps = {
  user: User;
  onEdit?: (user: User) => void;
  onDelete?: (userId: string) => void;
};

type ModalProps = {
  onSave: (user: User | Omit<User, "id">) => void;
  onCancel: () => void;
  user?: User;
};

const API_BASE_URL = "http://192.168.110.100:8080/data1";
const USERS_PER_PAGE = 6;

const UserCard = React.memo(({ user, onEdit, onDelete }: UserCardProps) => {
  const [isDeleting, setIsDeleting] = useState(false);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);

  const handleDelete = useCallback(async () => {
    if (!onDelete) return;

    setIsDeleting(true);
    try {
      await onDelete(user.id);
    } finally {
      setIsDeleting(false);
      setShowDeleteConfirm(false);
    }
  }, [onDelete, user.id]);

  const roleStyles = {
    admin: "bg-purple-100 text-purple-800",
    manager: "bg-blue-100 text-blue-800",
    user: "bg-gray-100 text-gray-800",
  };

  return (
    <div className="bg-white rounded-lg shadow-md overflow-hidden transition-all duration-300 hover:shadow-lg border border-gray-100">
      <div className="p-5">
        <div className="flex items-center space-x-4 mb-4">
          <div className="bg-gradient-to-r from-blue-500 to-indigo-600 rounded-full h-12 w-12 flex items-center justify-center text-white font-bold text-lg">
            {user.name[0]}
          </div>
          <div>
            <h2 className="text-lg font-semibold text-gray-800">{user.name}</h2>
            <span className={`text-xs px-2 py-1 rounded-full ${roleStyles[user.role]}`}>
              {user.role}
            </span>
          </div>
        </div>

        <div className="space-y-2 text-sm text-gray-600">
          <UserDetail icon="email" value={user.email || "-"} />
          <UserDetail icon="phone" value={user.phone} />
          <UserDetail icon="address" value={user.address} />
        </div>
      </div>

      <UserActions 
        user={user}
        onEdit={onEdit}
        onDelete={onDelete}
        isDeleting={isDeleting}
        showDeleteConfirm={showDeleteConfirm}
        setShowDeleteConfirm={setShowDeleteConfirm}
        handleDelete={handleDelete}
      />
    </div>
  );
});

