const Modal = ({ title, description, children, onCancel }: { title: string; description: string; children: React.ReactNode; onCancel: () => void }) => (
  <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
    <div className="bg-white rounded-xl shadow-2xl w-full max-w-md overflow-hidden">
      <div className="bg-gradient-to-r from-blue-500 to-indigo-600 p-5 text-white">
        <h2 className="text-xl font-semibold">{title}</h2>
        <p className="text-blue-100 text-sm">{description}</p>
      </div>
      {children}
    </div>
  </div>
);

const FormField = ({
  label,
  name,
  type = "text",
  value,
  onChange,
  required = false,
}: {
  label: string;
  name: string;
  type?: string;
  value: string;
  onChange: (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => void;
  required?: boolean;
}) => (
  <div>
    <label className="block text-sm font-medium text-gray-700 mb-1">
      {label}
    </label>
    <input
      type={type}
      name={name}
      value={value}
      onChange={onChange}
      className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition"
      required={required}
    />
  </div>
);

const user = ({ users: initialUsers = [] }: { users?: User[] }) => {
  const [users, setUsers] = useState<User[]>(initialUsers);
  const [search, setSearch] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showAddModal, setShowAddModal] = useState(false);
  const [editingUser, setEditingUser] = useState<User | null>(null);
  const [currentPage, setCurrentPage] = useState(1);
  const router = useRouter();

  const fetchUsers = useCallback(async () => {
    try {
      const response = await axios.get(API_BASE_URL);
      console.log("API Response:", response.data);

      let usersData = response.data;
      if (usersData && typeof usersData === "object" && !Array.isArray(usersData)) {
        if (Array.isArray(usersData.data)) {
          usersData = usersData.data;
        } else if (Array.isArray(usersData.users)) {
          usersData = usersData.users;
        }
      }

      if (!Array.isArray(usersData)) {
        throw new Error("API did not return an array of users");
      }

      const processedUsers = usersData.map((u: any, index: number) => ({
        id: u.id?.toString() || `generated-${index}-${Date.now()}`,
        name: u.name || "No name",
        phone: u.phone || "No phone",
        address: u.address || "No address",
        role: u.role || "user",
        email: u.email || "",
      }));

      const idSet = new Set();
      const duplicates = processedUsers.filter((user) => {
        if (idSet.has(user.id)) {
          console.warn(`Duplicate user ID detected: ${user.id}`);
          return true;
        }
        idSet.add(user.id);
        return false;
      });

      if (duplicates.length > 0) {
        console.error("Duplicate users found:", duplicates);
        throw new Error("Duplicate user IDs detected in API response");
      }

      setUsers(processedUsers);
    } catch (err) {
      console.error("Fetch error:", err);
      setError(err instanceof Error ? err.message : "Unknown error occurred");
      setUsers([]);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchUsers();
  }, [fetchUsers]);

  const handleSaveNewUser = useCallback(async (newUser: Omit<User, "id">) => {
    try {
      const response = await axios.post(API_BASE_URL, newUser);
      const createdUser = {
        ...newUser,
        id: response.data.id || `generated-${Date.now()}`,
      };
      setUsers(prev => [...prev, createdUser]);
      setShowAddModal(false);
      setCurrentPage(Math.ceil((users.length + 1) / USERS_PER_PAGE));
    } catch (err) {
      console.error("Error adding user:", err);
      setError("Gagal menambahkan user");
    }
  }, [users.length]);

  const handleSaveEditedUser = useCallback(async (editedUser: User) => {
    try {
      await axios.put(`${API_BASE_URL}/${editedUser.id}`, editedUser);
      setUsers(prev => prev.map(u => u.id === editedUser.id ? editedUser : u));
      setEditingUser(null);
    } catch (err) {
      console.error("Error updating user:", err);
      setError("Gagal mengupdate user");
    }
  }, []);

  const handleDeleteUser = useCallback(async (userId: string) => {
    try {
      await axios.delete(`${API_BASE_URL}/${userId}`);
      setUsers(prev => prev.filter(u => u.id !== userId));
      setCurrentPage(prev => 
        prev > Math.ceil((users.length - 1) / USERS_PER_PAGE) 
          ? Math.ceil((users.length - 1) / USERS_PER_PAGE) 
          : prev
      );
    } catch (err) {
      console.error("Error deleting user:", err);
      setError("Gagal menghapus user");
    }
  }, [users.length]);

  const filteredUsers = useMemo(() => {
    const searchTerm = search.toLowerCase();
    return users.filter(
      u =>
        u.name.toLowerCase().includes(searchTerm) ||
        u.phone.includes(search) ||
        u.address.toLowerCase().includes(searchTerm) ||
        u.role.toLowerCase().includes(searchTerm) ||
        (u.email && u.email.toLowerCase().includes(searchTerm))
    );
  }, [users, search]);

  const paginate = useCallback((pageNumber: number) => setCurrentPage(pageNumber), []);

  const indexOfLastUser = currentPage * USERS_PER_PAGE;
  const indexOfFirstUser = indexOfLastUser - USERS_PER_PAGE;
  const currentUsers = filteredUsers.slice(indexOfFirstUser, indexOfLastUser);
  const totalPages = Math.ceil(filteredUsers.length / USERS_PER_PAGE);

  if (loading) return <LoadingScreen />;
  if (error) return <ErrorScreen error={error} />;

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex">
      <Sidebar />
      <main className="flex-1 p-8 ml-64">
        <PageHeader onAddUser={() => setShowAddModal(true)} />
        
        <SearchBar search={search} setSearch={setSearch} />
        
        {filteredUsers.length === 0 ? (
          <EmptyState 
            hasUsers={users.length > 0} 
            onAddUser={() => setShowAddModal(true)} 
          />
        ) : (
          <>
            <UserGrid 
              users={currentUsers} 
              onEdit={(user) => setEditingUser(user)} 
              onDelete={handleDeleteUser} 
            />
            
            {totalPages > 1 && (
              <Pagination 
                currentPage={currentPage} 
                totalPages={totalPages} 
                paginate={paginate} 
              />
            )}
          </>
        )}

        {showAddModal && (
          <AddUserModal
            onSave={handleSaveNewUser}
            onCancel={() => setShowAddModal(false)}
          />
        )}

        {editingUser && (
          <EditUserModal
            user={editingUser}
            onSave={handleSaveEditedUser}
            onCancel={() => setEditingUser(null)}
          />
        )}
      </main>
    </div>
  );
};

// Additional Components
