const SearchBar = ({ search, setSearch }: { search: string; setSearch: (value: string) => void }) => (
  <div className="mb-6">
    <div className="relative max-w-md">
      <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
        <SearchIcon className="w-5 h-5 text-gray-400" />
      </div>
      <input
        type="text"
        placeholder="Cari pengguna..."
        value={search}
        onChange={(e) => setSearch(e.target.value)}
        className="pl-10 pr-4 py-2.5 w-full rounded-lg border border-gray-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition text-gray-900"
      />
    </div>
  </div>
);

const EmptyState = ({ hasUsers, onAddUser }: { hasUsers: boolean; onAddUser: () => void }) => (
  <div className="bg-white rounded-xl shadow-sm p-8 text-center">
    <EmptyIcon className="w-16 h-16 mx-auto text-gray-400 mb-4" />
    <h3 className="text-lg font-medium text-gray-900 mb-1">
      {hasUsers ? "Pengguna tidak ditemukan" : "Belum ada pengguna"}
    </h3>
    <p className="text-gray-500 mb-4">
      {hasUsers ? "Coba dengan kata kunci lain" : "Mulai dengan menambahkan pengguna baru"}
    </p>
    {!hasUsers && (
      <button
        onClick={onAddUser}
        className="px-4 py-2 bg-gradient-to-r from-blue-500 to-indigo-600 text-white rounded-lg font-medium hover:from-blue-600 hover:to-indigo-700 transition shadow-md"
      >
        Tambah Pengguna Pertama
      </button>
    )}
  </div>
);

const UserGrid = ({ users, onEdit, onDelete }: { users: User[]; onEdit: (user: User) => void; onDelete: (userId: string) => void }) => (
  <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
    {users.map((user) => (
      <UserCard
        key={`user-${user.id}`}
        user={user}
        onEdit={onEdit}
        onDelete={onDelete}
      />
    ))}
  </div>
);

const Pagination = ({ currentPage, totalPages, paginate }: { currentPage: number; totalPages: number; paginate: (page: number) => void }) => (
  <div className="flex justify-center mt-8">
    <nav className="inline-flex rounded-md shadow-sm -space-x-px" aria-label="Pagination">
      <PaginationButton
        onClick={() => paginate(Math.max(1, currentPage - 1))}
        disabled={currentPage === 1}
        direction="prev"
      />
      
      {Array.from({ length: totalPages }, (_, i) => i + 1).map((number) => (
        <PaginationButton
          key={number}
          onClick={() => paginate(number)}
          active={currentPage === number}
        >
          {number}
        </PaginationButton>
      ))}
      
      <PaginationButton
        onClick={() => paginate(Math.min(totalPages, currentPage + 1))}
        disabled={currentPage === totalPages}
        direction="next"
      />
    </nav>
  </div>
);

const PaginationButton = ({ 
  children, 
  onClick, 
  disabled = false, 
  active = false, 
  direction 
}: {
  children?: React.ReactNode;
  onClick: () => void;
  disabled?: boolean;
  active?: boolean;
  direction?: "prev" | "next";
}) => (
  <button
    onClick={onClick}
    disabled={disabled}
    className={`px-4 py-2 border ${
      active
        ? "bg-blue-50 border-blue-500 text-blue-600"
        : disabled
        ? "bg-gray-100 border-gray-300 text-gray-400 cursor-not-allowed"
        : "bg-white border-gray-300 text-gray-500 hover:bg-gray-50"
    } ${direction === 'prev' ? 'rounded-l-md' : ''} ${direction === 'next' ? 'rounded-r-md' : ''}`}
  >
    {direction ? (
      <>
        <span className="sr-only">{direction === 'prev' ? 'Previous' : 'Next'}</span>
        {direction === 'prev' ? <PrevIcon /> : <NextIcon />}
      </>
    ) : (
      children
    )}
  </button>
);

